.menu-wrapper {
	list-style: none;
	margin: 0;
	min-width: 180px;
	// height: 0;
	max-height: 320px;
	padding: 16px 12px;
	background-color: var(--background-sec);
	color: var(--text-color);
	transition: all 0.5s ease-out;
	border: 1px solid var(--text-color);
	border-radius: 9px;
	overflow: auto;
}
.menu-item {
    padding: 8px 0;
	display: flex;
    justify-content: space-between;
	gap: 5px;
	align-items: center;
	font-size: 1rem;
	text-transform: capitalize;
	font-family: 'Titillium Web', sans-serif;
	font-weight: 300;
}
