
.App-main{
    display: flex;
    flex-direction: column-reverse;
     margin: 0 auto;
     margin-top: 60px;
     max-height: calc(100vh - var(--H-height));
     height: calc(100vh - var(--H-height));
     padding: 15px;
     gap: 15px;
}
.App-content{
     flex-grow: 1;
     padding: 15px;
     border: 2px solid var(--border-color);
     border-radius: 6px;
     overflow: overlay;
}
.App-view{
     display: flex;
     height: 100%;
     flex-wrap: wrap;
     justify-content: center;
     align-items: center;
     gap: 15px;
}
@media screen and (min-width:920px) {
     .App-main{
          flex-direction: row;
     }
}

.set-input:not([type="checkbox"]){
     -webkit-appearance: none;
     appearance: none;
     background-color: transparent;
     border: none;
     border-bottom: 2px dotted var(--border-color);
     color: inherit;
     font-size: 1rem;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
}
.set-input:invalid{
     color: red;
}
.set-input:focus{
     outline: none;
     border-color: var(--LimeGreen)
}

.Header-item{
     display: flex;
     gap:5px;
}
.Header-item:not(:first-child){margin-left: 25px;}