.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: var(--background-sec);
    z-index: 999;
    .wrapper{
        margin: auto;
        max-width: 1250px;
        padding: 15px;
        height: var(--H-height);
        display: flex;
         justify-content: space-between;
        align-items: center;
    }
    .heading{
        font-family: 'Noto Serif SC', serif;
        font-size: 1.6rem;
        font-weight: 700;
        margin-right: auto;
    }
}
