.wrapper {
	width: 100%;
	margin-right: 16px;
	border-radius: 6px;
	flex-shrink: 1;
	*[tabindex]:focus {
		border: none;
		outline: 2px var(--BrightRed) solid;
		border: 2px solid transparent;
	}
	form {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;

		.form-btn-group {
			gap: 5px;
			display: flex;
			width: 100%;
			margin-top: 5px;
		}
		button {
			background-color: transparent;
			font-size: 1.5rem;
			width: 15%;
			min-height: 60px;
			height: 100%;
			cursor: pointer;
			flex-grow: 1;
			border: 2px solid var(--border-color);

			border-radius: 6px;
			color: inherit;
			&:disabled {
				pointer-events: none;
				opacity: 0.25;
				cursor: not-allowed;
			}
			&:active {
				border-color: red;
			}
		}
	}
}
.text-area {
	font-family: "Noto Serif SC", serif;
	font-size: 1.25rem;
	font-weight: 300;
	background-color: transparent;
	border: 2px solid var(--border-color);
	padding: 15px;
	flex-grow: 1;
	width: 100%;
	height: 100%;
	font-size: 1.6rem;
	resize: none;
	color: inherit;
	border-radius: 9px;
}

@media screen and (min-width: 920px) {
	.wrapper {
		width: 320px;
		flex-shrink: 0;
		form {
			.form-btn-group {
				margin-top: 5px;
			}
			button {
				width: 100%;
				height: unset;
				margin-left: 0;
				margin-top: 5px;
			}
		}
	}
}
