.loading-wrapper{
    position : absolute;
    top: 50%;left: 50%;
    transform: translate(-50% ,-50%);
    .loading{
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 2px solid #ddd;
        border-top-color: transparent;
        border-bottom-color:transparent ;
        animation: spin 1s ease infinite;
    }
    @keyframes spin {
        100%{
            transform: rotate(360deg);
        }
    }
}