.wrapper{
    min-width: 180px;
    min-height: 202px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--background-sec);
    border: 2px solid transparent;
    border-radius: 6px;
    overflow: hidden;
    position: relative; 
}
.Element-Diplay-Hanzi { 
    min-width:180px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.quiz-info{
    position: absolute;
    top: 0;
    right: 0;
    
   padding: 15px 5px;
    color: inherit;
    gap: 5px;
}
.controler{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
     padding: 5px 15px;
     background-color: var(--background-sec);
     transform: translateY(100%);
     transition: transform 0.350s linear;
     .btn{
         transition: color 0.350s linear;
         color: inherit;
         background-color: transparent;
         border: none;
         outline: none;
         padding: 5px;
         cursor: pointer;
     }
     .right-section{
         display: flex;
         align-items: center;
         gap: 5px;
     }
}
.wrapper:hover .controler{transform: translateY(0);}