@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+SC:wght@300;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300&display=swap');
@import "normalize.css";

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html {
	user-select: none;
}
html *::-webkit-scrollbar {
	border-radius: 0;
	width: 8px;
}
html *::-webkit-scrollbar-thumb {
	border-radius: 4px;
	background-color: rgba(0, 0, 0, 0.15);
}
html *::-webkit-scrollbar-track {
	border-radius: 0;
	background-color: rgba(0, 0, 0, 0);
}

:root {
	--H-height: 60px;
	--LimeGreen: hsl(163, 72%, 41%);
	--BrightRed: hsl(356, 69%, 56%);
	--white: hsl(0, 0%, 100%);
	// light theme
	--background-pri: hsl(225, 100%, 98%);
	--background-sec: hsl(227, 47%, 96%);
	--text-color: hsl(230, 17%, 14%);
	--border-color: hsl(228, 12%, 44%);
	// dark theme
	--VeryDarkBlueD: hsl(230, 17%, 14%);
	--VeryDarkBlueD1: hsl(232, 19%, 15%);
	--DarkDesaturatedBlue: hsl(228, 28%, 20%);
	--DesaturatedBlue: hsl(228, 34%, 66%);
}

body {
	background-color: var(--background-pri);
	color: var(--text-color);
	transition: all 0.5s ease-out;
}
body.dark-mode {
	--background-pri: hsl(230, 17%, 14%);
	--background-sec: hsl(232, 19%, 15%);
	--text-color: hsl(228, 34%, 66%);
	--border-color: hsl(228, 28%, 20%);
	transition: all 0.5s ease-out;
	background-color: var(--background-pri);
	color: var(--text-color);
}
